body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body{
  background-image: url("../src/images/ukraine_flag.jpg");
  height: 100%;
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
  min-height: 100vh
}
  
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.container{
  display: flex;
  flex-direction: column;
  width: 400px;
}

.submit_form{
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 250px;
  border: 1px solid;
  padding: 5px;
}

.label{
  display: flex;
  flex-direction: column;
  width: 50%;
  gap: 10px;
}

.submit_btn{
  width: 100px;
  background-color: #fff;
  border: 1px solid;
  border-radius: 5px;
}

.delete_btn {
  margin-left: 20px;
  width: 70px;
  background-color: #fff;
  border: 1px solid;
  border-radius: 5px;
}
